.pagina-contato{
    display: flex;
    flex-direction: column;
    background-color: #fff;
    align-items: center;
    justify-content: center;

    h1{
        margin-top: 100px;
        margin-bottom: 50px;
        font: 600 30px 'K2D';
    }

    .cappuccino{
        display: flex;
        width: 100%;
        position: relative;

    }

    .interativo{
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-bottom: 100px;
    }
    
    .cima{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: row;
        gap: 10px;
    }

    .campos{
        align-items: center;
        justify-content: center;
        display: flex;
        flex-direction: column;
        gap:10px;
        
    }

    .campos input{
        width: 550px;
        height: 40px;
        border-radius: 10px;
        padding: 10px 25px;
        background-color: #FFDACF;
        border: none;
    }

    .mensagem textarea{
        width: 860px;
        height: 100px;
        border-radius: 10px;
        padding: 15px 25px;
        background-color: #FFDACF;
        border: none;
        font: 500 14px 'K2D';
    }
    

    .politica{
        display: flex;
        align-items: center;
        gap: 15px;
        justify-content: flex-start;
    }

    .escrita{
        display: flex;
        gap: 5px;
        font: 600 12px 'K2D';
    }

    button{
        font: 500 20px 'K2D';
        width: 150px;
        padding: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        border: none;
        border-radius: 10px;
        background-color: #FFDACF;
        cursor: pointer;
    }

    #picture__input {
        display: none;
      }
      
      .picture {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 300px;
            height: 190px;
            background-color: #FFDACF;
            border: 2px dashed #50301e;
            border-radius: 10px;
            cursor: pointer;
      }
      
      .picture:hover {
        color: #50301e;
        background: #ffccbc;
      }
      
      .picture__img {
        max-width: 70%;
      }      

    button:hover{
        background-color: #ffccbc ;
        
    }

    input:hover{
        background-color: #ffccbc ;
        cursor: pointer;
    }
    
    textarea:hover{
        background-color: #ffccbc ;
        cursor: pointer;
    }
    
}